import { BaseValueObject } from '../Base';

export class BearerToken extends BaseValueObject<'BearerToken', string> {
  static from(value: string): BearerToken {
    return new BearerToken(value);
  }

  protected validate(value: string): string {
    if (!value || value.trim().length === 0) {
      throw new Error('BearerToken is required');
    }

    return value;
  }
}
